
          @import "@/assets/scss/variables.scss";
        

// 메인박스
.main-wrapper {
  /* 플렉스가 좋을지 그리드가 좋을지 */
  //display: flex;
  //align-items: center;
  //justify-content: center;
  //flex-wrap: wrap;

  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));

  gap: 26px;
  height: 100%;
  background-color: #F8F8F8;
}
// 해당 메인페이지 전용 애니메이션
.fade-enter-active,
.fade-leave-active {
  transition: opacity 1s ease, transform 1s ease;
}

.fade-enter-from,
.fade-leave-to {
  opacity: 0;
  transform: translateY(20px);
}
.fade-enter-to,
.fade-leave-from {
  opacity: 1;
  transform: translateY(0);
}

